import React, { useContext } from "react";
import styled from "styled-components";
import { LanguageContext } from "../contexts/Language";
import EmptyBox from "../components/EmptyBox";
import text_dingdinguniv from "../assets/text_dingdinguniv.svg";
import text_dingdinguniv_eng from "../assets/text_dingdinguniv_eng.svg";
import logo_dingdingeye from "../assets/logo_dingdingeye.svg";
import logo_dingcok from "../assets/logo_dingcok.svg";
import logo_dingdingeye_small from "../assets/logo_dingdingeye_small.svg";
import logo_dingcok_small from "../assets/logo_dingcok_small.svg";

const Title = styled.div`
  font-family: "GmarketSansBold", sans-serif;
  font-size: ${(props) => (props.language === "ko" ? 95 : 65)}px;
  letter-spacing: -0.06em;
  color: #000000;
  margin-left: ${(props) => (props.language === "ko" ? 475 : 452)}px;
  white-space: pre-line;
  line-height: ${(props) => (props.language === "ko" ? 138.6 : 120.5)}%;

  @media screen and (max-width: 665px) {
    font-size: 30px;
    margin-left: 25px;
  }
`;
const HorizontalContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: flex-start;
`;
const DingDingimg = styled.img`
  @media screen and (max-width: 665px) {
    width: 111px;
    margin-top: 10px;
  }
`;
const Content = styled.div`
  font-family: "GmarketSansMedium", sans-serif;
  font-size: 23px;
  letter-spacing: -0.02em;
  color: #000000;
  margin-left: ${(props) => (props.language === "ko" ? 475 : 452)}px;
  white-space: pre-line;
  margin-bottom: 13px;

  @media screen and (max-width: 665px) {
    font-size: 12px;
    margin-top: 80px;
    margin-left: 25px;
  }
`;
const LocationInfo = styled.div`
  font-family: "GmarketSansBold", sans-serif;
  font-size: 40px;
  letter-spacing: -0.06em;
  color: #000000;
  margin-top: ${(props) => (props.language === "ko" ? 463 : 427)}px;
  margin-left: 292px;

  @media screen and (max-width: 665px) {
    font-size: 40px;
    margin-top: 279px;
    margin-left: 49px;
  }
`;
const Location = styled.div`
  font-family: "GmarketSansMedium", sans-serif;
  font-size: 23px;
  color: #000000;
  margin-top: 69px;
  margin-left: 292px;
  white-space: pre-line;
  margin-bottom: 13px;

  @media screen and (max-width: 665px) {
    font-size: 23px;
    margin-top: 75px;
    margin-left: 49px;
  }
`;

const Company = () => {
  const { language } = useContext(LanguageContext);

  return (
    <div id="company">
      <EmptyBox height={429} />
      {language === "ko" ? (
        <>
          <HorizontalContainer>
            <div>
              <Title>MBC {"\u00A0"}</Title>
            </div>
            <div>
              <DingDingimg
                src={text_dingdinguniv}
                alt={text_dingdinguniv}
                style={{ marginBottom: 10 }}
              />
            </div>
            <div>
              <Title style={{ marginLeft: 0 }}></Title>
            </div>
          </HorizontalContainer>
          <Title>  </Title>
          <Title>  .</Title>
          <EmptyBox height={21} />
          <HorizontalContainer>
            <Content>
              AI     {"\u00A0"}
            </Content>
            <img
              src={logo_dingdingeye}
              alt={logo_dingdingeye}
              style={{ width: 77, marginBottom: 20 }}
            />
          </HorizontalContainer>
          {window.innerWidth <= 665 ? (
            <div>
              <HorizontalContainer>
                <Content>
                       {"\u00A0"}
                </Content>
                <img src={logo_dingcok_small} alt={logo_dingcok} />
              </HorizontalContainer>
              <Content>    .</Content>
            </div>
          ) : (
            <HorizontalContainer>
              <Content>
                     {"\u00A0"}
              </Content>
              <img src={logo_dingcok_small} alt={logo_dingcok} />
              <Content style={{ marginLeft: 0 }}>
                ,     .
              </Content>
            </HorizontalContainer>
          )}
        </>
      ) : (
        <>
          <Title>MBC's in-house venture,</Title>
          <img
            src={text_dingdinguniv_eng}
            alt={text_dingdinguniv_eng}
            style={{ marginLeft: 452 }}
          />
          <Title>creates value through the utilization</Title>
          <Title>of public broadcasting archives.</Title>
          <EmptyBox height={105} />
          <HorizontalContainer>
            <Content>
              We produce content such as the AI-driven automatic metadata
              generation solution{"\u00A0"}
            </Content>
            <img
              src={
                window.innerWidth <= 665
                  ? logo_dingdingeye_small
                  : logo_dingdingeye
              }
              alt={logo_dingdingeye}
              style={{ width: 77, marginBottom: 20 }}
            />
          </HorizontalContainer>
          <HorizontalContainer>
            <Content>
              and dementia management content using broadcast archives{"\u00A0"}
            </Content>
            <img
              src={window.innerWidth <= 665 ? logo_dingcok_small : logo_dingcok}
              alt={logo_dingcok}
              style={{ width: 33, marginBottom: 20 }}
            />
          </HorizontalContainer>
          <Content>contributing to knowledge creation.</Content>
        </>
      )}

      <LocationInfo>
        {language === "ko" ? " " : "Contact"}
      </LocationInfo>
      <Location>
        {language === "ko"
          ? "   267, MBC  ()\n02--789-2559 / dingding.univ@gmail.com"
          : "267, Seongam-ro, Mapo-gu, Seoul, MBC New Building (Sangam-dong)\n02-789-2559 / dingding.univ@gmail.com"}
      </Location>
      <EmptyBox height={100} />
    </div>
  );
};

export default Company;
